<template>
  <div class="fk">
    <div>
      <headers :title="title"></headers>
      <div class="userMsg">
        <img class="icon1" src="../assets/fk/icon1.png" alt="">
        <div class="msg">
          <p class="user">
            <span>收货人：<i>郭靖</i></span>
            <span>13388888888</span>
          </p>
          <p class="address">收货地址：重庆重庆市渝北区回兴街道渝北西政校区</p>
          <p class="tishi">(收货不便时，可选择免费代收货服务)</p>
        </div>
        <img class="icon2" src="../assets/fk/icon2.png" alt="">
      </div>
      <div class="sp">
        <h2><van-checkbox v-model="checked">伊衫奇恋男衣店</van-checkbox><img src="../assets/mdx/travel_search_icon_arrow_right.png" alt=""></h2>
        <div class="details">
          <img class="yifu" src="../assets/mdx/yifu.png" alt="">
          <div class="detail">
            <p class="name">
              <span>男士春秋纯棉套头长袖卫衣男圆领宽松休闲运动大码卫衣潮学生上衣</span>
            </p>
            <p class="haoma">颜色：黑绒ty，尺码：2XL</p>
            <van-tag plain type="danger">七天退换</van-tag>
            <p class="jiage"><span>￥<i>89</i>.9</span><span>1</span></p>
          </div>
        </div>
      </div>
      <van-cell-group>
        <van-cell title="店铺优惠" is-link value="无"></van-cell>
        <van-cell title="配送方式" is-link value="快递 免邮"></van-cell>
        <van-cell title="运送费" is-link value="卖家送，确认收货前退货可赔"></van-cell>
        <van-field label="买家留言：" v-model="value" placeholder="选填：对本次交易的说明（建议填写已和卖）"></van-field>
        <p><a href="#">共一件商品</a><i>小计：<span>￥89.<e>90</e></span></i></p>
      </van-cell-group>
    </div>
    <div class="footer">
      <i>合计：<span>￥89.<e>90</e></span></i>
      <a href="#">结算（0）</a>
    </div>
  </div>
</template>

<style lang='less' scoped>
  .fk {
    background: #f6f6f6;
    flex: 1;
    font-family: 'PingFang-SC-Medium';
    display: flex;
    flex-direction: column;
    .userMsg {
      margin-top: 90px;
      background: #fff url('../assets/qrdd/msg.png') no-repeat center bottom;
      background-size: 100% 14px;
      padding: 0 34px 14px;
      height: 192px;
      border-bottom: 1px solid #e5e5e5;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .icon1 {
        width: 36px;
        height: 72px;
      }
      .msg {
        flex: 1;
        padding-left: 24px;
        .user {
          display: flex;
          justify-content: space-between;
          font-size: 28px;
          i {
            font-size: 32px;
            font-style: normal;
          }
        }
        .address {
          font-size: 26px;
          padding: 20px 32px 20px 0;
        }
        .tishi {
          font-size: 24px;
          color: #e9a945;
        }
      }
      .icon2 {
        width: 18px;
        height: 60px;
      }
    }
    .sp {
      background: #fff;
      margin-top: 20px;
      padding-left: 30px;
      font-size: 24px;
      color: #333;
      padding-bottom: 10px;
      .details {
        background: #fafafa;
        display: flex;
        align-items: center;
      }
      h2 {
        height: 82px;
        line-height: 82px;
        display: flex;
        align-items: center;
        .van-checkbox {
          width: 312px;
          background: #fff;
        }
        img {
          width: 18px;
          height: 30px;
        }
      }
      .yifu {
        width: 224px;
        height: 206px;
        float: left;
      }
      .detail {
        flex: 1;
        padding-right: 30px;
        padding-bottom: 20px;
        .name {
          display: flex;
          justify-content: space-between;
          font-size: 24px;
          line-height: 34px;
          margin-top: 16px;
          .van-icon-edit {
            width: 26px;
          }
        }
        .haoma {
          font-size: 24px;
          color: #999999;
          margin-top: 15px;
          margin-bottom: 14px;
        }
        .jiage {
          margin-top: 20px;
          font-size: 24px;
          line-height: 24px;
          color: #ec612a;
          display: flex;
          justify-content: space-between;
          i {
            font-size: 28px;
            padding-left: 8px;
          }
          span:last-child {
            font-size: 24px;
            color: #000;
            padding-left: 13px;
            background: url('../assets/mdx/cheng.png') no-repeat bottom 1px left;
            background-size: 13px 16px;
          }
        }
      }
    }
    .van-cell-group p{
      font-size: 24px;
      height: 90px;
      line-height: 90px;
      text-align: right;
      i {
        padding: 0 30px;
        color: #000;
        span {
          color: #653a72;
          e {
            font-size: 22px;
          }
        }
      }
    }
    .footer {
      height: 108px;
      background: #fff;
      display: flex;
      font-size: 30px;
      line-height: 108px;
      align-items: center;
      position: fixed;
      bottom: 0;
      width: 100%;
      i {
        flex: 1;
        margin-left: 276px;
        color: #000;
        span {
          color: #653a72;
          e {
            font-size: 22px;
          }
        }
      }
      a {
        width: 200px;
        background: #653a72;
        color: #fff;
        text-align: center;
      }
    }
  }
</style>

<script>
  import "lib-flexible"
  import headers from '../components/editHeader.vue'
  export default {
    components: {
      headers
    },
    data() {
      return {
        title: "确认订单",
        checked: '',
        value: ''
      }
    }
  }
</script>



